   margin-top: 10px;
}

.personal-project-header {
   display: flex;
   align-items: center;
   justify-content: space-between;
}

.add-icon-button {
   margin-right: max(2vw, 15px);
   border: none;
   width: fit-content;
   padding: 0;
   background: none;
   cursor: pointer;
}

.add-icon {
