}

input[type="tel"]:invalid:not(:focus) {
   background-color: rgb(255 209 209);
}

.project-name {
   font-weight: normal;
   margin: 0 0 4px 0;
}

#content h2,
#content p {
   margin: 0;
}

#content > .description-container,
#content > .checklist-container,
#content > .note-container {
   margin: 59px 0 0 20px;
